<template>
  <div class="report">
    <div class="box">
      <p class="title">{{title}}</p>
      <div class="img">
        <img
          class="img_content"
          v-for="(item,index) in number"
          :key="index"
          :src="require('.././assets/' + pathUrl + '/' + index + '.png')"
          ref="miImg"
        />
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      pathUrl: "",
      title: "",
      number: 0,
      selfColor: ""
    };
  },
  mounted() {
    let name = this.$route.name;
    switch (name) {
      case "biglevel":
        this.pathUrl = "大将军666禁将";
        this.title = "禁将方案一";
        this.number = 35;
        this.$nextTick(() => {
          let miImg = this.$refs.miImg;
          for (let i = 0; i < miImg.length; i++) {
            miImg[i].style.boxShadow = "yellow 0px 0px 10px 5px";
          }
        });
        break;
      case "smolllevel":
        console.log("smolllevel");
        this.pathUrl = "大将军禁将";
        this.title = "禁将方案二";
        this.number = 56;
        this.$nextTick(() => {
          let miImg = this.$refs.miImg;
          for (let i = 0; i < miImg.length; i++) {
            miImg[i].style.boxShadow = "red 0px 0px 10px 5px";
          }
        });
        break;
      case "shabilevel":
        this.pathUrl = "傻逼才选";
        this.title = "选到就赢";
        this.number = 50;
        this.$nextTick(() => {
          let miImg = this.$refs.miImg;
          for (let i = 0; i < miImg.length; i++) {
            miImg[i].style.boxShadow = "yellow 0px 0px 10px 5px";
          }
        });
        break;
    }
  }
};
</script>



<style lang="" scoped>
.title {
  color: red;
  font-size: 30px;
  font-weight: 1000;
  text-align: center;
}
.img_content {
  margin: 15px;
  border-radius: 25px;
  /* box-shadow: yellow 0px 0px 10px 5px; */
}
</style>



